//axiosaxios

//axiosaxios
import axios from "axios";
//
import nprogress from "nprogress";
//
import 'nprogress/nprogress.css'



//axios.create,axiosmockRequestsmockRequestsaxios
let mockRequests = axios.create({
    //,URLapi:/api
    baseURL: "/mock",
    //
    timeout: 5000
});

//:N,!!!
mockRequests.interceptors.request.use(config => {
    //:header,
    //,

    //
    nprogress.start()
    return config;
});


//
mockRequests.interceptors.response.use((res) => {
    //res:

    //
    nprogress.done()
    //respromise
    return res.data;
}, (err) => {
    //:,,
    alert(err.message);
    //Promise
    return new Promise();
});

//:axios,mockRequests
export default mockRequests;



